.winner-banner-container {
  background-color: rgba(0, 0, 0, .75);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  .winner-banner {
    background-color: var(--dark-bg);
    width: 20em;
    width: 20em;
    padding: 1.5rem;
    display: flex;
    flex-flow: column;
    align-items: center;
    p {
      margin: 0 0 1.5rem 0;
      font-size: 2rem;
    }
    button {
      font-size: 1rem;
      padding: .5rem 1rem;
    }
  }
}